.feed_page {
    display: grid;
    height: 100%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content;
    gap: 40px 60px;
}

.orders_wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-self: flex-start;
    gap: 60px 36px;
}

.done_order {
    grid-column: 1/3;
}
